.bottles {
    background: #FD434D;
    padding: 100px 0;
    min-height: 100vh;
    display: flex;
    align-items: center;
}

.bottles h2,.bottles p{
    color: #fff;
}
.galleryimages {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.galleryimages img {
    border-radius: 30px;
    margin-top: 30px;
}
.galleryimages img:nth-child(2) {
    margin-top: -30px;
}
.bottles h4 {
    margin: 0;
    font-size: 32px;
    margin-top: 40px;
    color: #fff;
    font-weight: 400;
}