.bluesea {
    background: url(../imgs/water.png);
    background-size: cover;
    background-position: center;
    padding: 100px 0;
    min-height: 100vh;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 2;
}
.bluesea::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #0F496B;
    opacity: .8;
    z-index: -1;
}
.bluesea h3 {
    color: #fff;
    font-size: 34px;
}
.iframe-video {
    width: 100%;
    border-radius: 50px; 
    overflow: hidden;
}
.iframe-video iframe {
    width: 100%;
    height: 360px;
    object-fit: cover;
}

.bluesea h2,.bluesea p{
    color: #fff;
}