.footer-wrp {
    text-align: center;
}

.footer {
    padding: 50px 0;
}

.footer-wrp p a {
    text-align: center;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 30px;
}

.footer-wrp li {
    list-style: none;
    display: inline-block;
}

.footer-wrp li a {
    padding: 8px 25px;
    border-radius: 100px;
    border: 3px solid #189FB1;
    margin: 0 10px;
    font-family: 'Josefin Sans', sans-serif;
    display: block;
    align-items: center;
    line-height: 16px;
    transition: .3s;
}

.footer-wrp li a i {
    color: #189FB1;
    margin-right: 10px;
    transition: .3s;
}

.footer-wrp li a:hover {
    background: #189FB1;
    color: #fff;
}

.footer-wrp li a:hover i {
    color: #fff;
}