@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.3/css/all.min.css);
/* 
font-family: 'Josefin Sans', sans-serif;
font-family: 'Playfair Display', serif;
*/
  
body{
    font-family: 'Playfair Display', serif;
    font-size: 26px;
    color: #424142;
    line-height: 400;
    line-height: 40px; 
}


a{
    text-decoration: none;
    color: #000;
    color: initial;
}
button{
    outline: none;
    box-shadow: none;
    border: none;
}
h1,
h2,
h3,
h4,
h5,
h6{
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 600;
    margin: 0;
    margin-bottom: 10px;
    color: #424142;
}
img{
    max-width: 100%;
}
.container{
    max-width: 1530px;
}
ul{
    margin: 0;
    padding: 0;
}






















































header {
    background: transparent;
    padding: 20px 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 99;
    transition: .3s;
}
header.stickyHeader {
    background: #217282;
}
.header-wrp {
    display: flex;
    align-items: center;
}
.logo a {
    display: block;
    width: 90px;
}


.hero-area {

    min-height: 100vh;
    padding-top: 320px;
    padding-bottom: 200px;
    position: relative;
    background: url(/react-testing-site/static/media/home.37ddd7e2.png);
    background-size: cover;
    background-position: center;
    z-index: 2;
}
.hero-area:after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: #0A6372;
    opacity: .78;
}
.hero-wrp h2 {
    color: #fff;
    font-size: 57px;
    margin-bottom: 20px;
}

.hero-wrp  p {
    margin: 0;
    color: #fff;
}
.hero-area a.theme-btn {
    position: absolute;
    bottom: 50px;
    left: 50%;
    transform: translateX(-50%);
}
























.social {
    width: 50%;
}

.social ul li {
    display: inline-block;
}

.social ul li a {
    position: relative;
    font-size: 20px;
    color: #fff;
    margin-right: 25px;
    text-decoration: none;
    display: inline-block;
}
.theme-btn {
    display: inline-block;
    padding: 10px 50px;
    background: #ED4451;
    color: #fff;
    transition: .3s;
}
.theme-btn:hover {
    background: #ED4451;
    color: #fff;
}
.wrp a.theme-btn {
    margin-top: 30px;
}
.wrp h2 {
    font-size: 57px;
}
.mission {
    background: #D8ECF3;
    padding: 50px;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.vodka {
    background: #1BA5B7;
    padding: 100px 0;
    min-height: 100vh;
    display: flex;
    align-items: center;
}

.vodka h2,.vodka p{
    color: #fff;
}
.OVOlution { 
    padding: 100px 0;
    min-height: 100vh;
    display: flex;
    align-items: center;
}
 
.blog {
    background: #D9ECF2;
    padding: 100px 0;
    min-height: 100vh;
    display: flex;
    align-items: center;
}
.blog-block img {
    width: 100%;
    height: 300px;
    border-radius: 30px;
    margin-bottom: 50px;
}
 
.blog-block .wrp h2 {
    font-size: 40px;
}
.bottles {
    background: #FD434D;
    padding: 100px 0;
    min-height: 100vh;
    display: flex;
    align-items: center;
}

.bottles h2,.bottles p{
    color: #fff;
}
.galleryimages {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.galleryimages img {
    border-radius: 30px;
    margin-top: 30px;
}
.galleryimages img:nth-child(2) {
    margin-top: -30px;
}
.bottles h4 {
    margin: 0;
    font-size: 32px;
    margin-top: 40px;
    color: #fff;
    font-weight: 400;
}
.bluesea {
    background: url(/react-testing-site/static/media/water.7b8f5929.png);
    background-size: cover;
    background-position: center;
    padding: 100px 0;
    min-height: 100vh;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 2;
}
.bluesea::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #0F496B;
    opacity: .8;
    z-index: -1;
}
.bluesea h3 {
    color: #fff;
    font-size: 34px;
}
.iframe-video {
    width: 100%;
    border-radius: 50px; 
    overflow: hidden;
}
.iframe-video iframe {
    width: 100%;
    height: 360px;
    object-fit: cover;
}

.bluesea h2,.bluesea p{
    color: #fff;
}
.footer-wrp {
    text-align: center;
}

.footer {
    padding: 50px 0;
}

.footer-wrp p a {
    text-align: center;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 30px;
}

.footer-wrp li {
    list-style: none;
    display: inline-block;
}

.footer-wrp li a {
    padding: 8px 25px;
    border-radius: 100px;
    border: 3px solid #189FB1;
    margin: 0 10px;
    font-family: 'Josefin Sans', sans-serif;
    display: block;
    align-items: center;
    line-height: 16px;
    transition: .3s;
}

.footer-wrp li a i {
    color: #189FB1;
    margin-right: 10px;
    transition: .3s;
}

.footer-wrp li a:hover {
    background: #189FB1;
    color: #fff;
}

.footer-wrp li a:hover i {
    color: #fff;
}
