header {
    background: transparent;
    padding: 20px 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 99;
    transition: .3s;
}
header.stickyHeader {
    background: #217282;
}
.header-wrp {
    display: flex;
    align-items: center;
}
.logo a {
    display: block;
    width: 90px;
}


.hero-area {

    min-height: 100vh;
    padding-top: 320px;
    padding-bottom: 200px;
    position: relative;
    background: url(../imgs/home.png);
    background-size: cover;
    background-position: center;
    z-index: 2;
}
.hero-area:after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: #0A6372;
    opacity: .78;
}
.hero-wrp h2 {
    color: #fff;
    font-size: 57px;
    margin-bottom: 20px;
}

.hero-wrp  p {
    margin: 0;
    color: #fff;
}
.hero-area a.theme-btn {
    position: absolute;
    bottom: 50px;
    left: 50%;
    transform: translateX(-50%);
}























